import { Link } from 'react-router-dom';
import { links, headerGnbDepth2, headerGnbSitemap } from '../../data/memo';
import { useState, useEffect } from 'react';

import './../../styles/layout/_header.scss';

// ?   , header class   
// ?   , header focus 
// window.addEventListener('popstate', () => {
//   console.log("change"); //     . 
// })

export default function Header() {

  // ? main  header-main  set
  const [main, setMain] = useState(true);

  const [depth2, setDepth2] = useState({
    index: -1,
    state: 'depth2-close',
    focusIndex: -1,
  });
  
  const definePage = () => {
    // ? pathname  main   method
    const thisPage = (path: string) => {
      if (path === 'OSP3111') { 
        window.addEventListener('scroll', () => {
          if(window.scrollY > 0){
            // scroll  sub header 
            setMain(false);
          }
          else if(window.scrollY === 0){
            // scroll     main header 
            setMain(true);
          }
        });

        setMain(true);
      } 

      else {
        // ! temp 
        window.addEventListener('scroll', () => {
          setMain(false);
        })
        setMain(false);
      }
    };

    thisPage(window.location.pathname.split('/').pop()); // string
  };

  useEffect(() => {
    definePage(); //  
  }, []);

  // gnb , 
  const onClickGnb = () => {
    definePage();
  };

  let displayTimer;
  const onMouseEnter = (index: number = 0) => {
    setDepth2({
      ...depth2,
      index,
      state: 'depth2-open',
    });

    clearTimeout(displayTimer);
  };

  const onMouseLeave = () => {
    displayTimer = setTimeout(() => {
      setDepth2({ ...depth2, state: 'depth2-close' });
    }, 300);
  };

  const onClick = (num) => {
    // click page  
    window.scrollTo(0, 0); 

    setDepth2({
      ...depth2,
      state: 'depth2-close',
      focusIndex: num,
    });

  };


  return <header className={main? 'header header-main': 'header header-sub'}>
    <nav>

      <div className='gnb__depth1 width1440'>
        <div className="--left"
          onClick={() => { onClickGnb() }}>

          <div className="depth1__logo">
            <Link to={links.mainHome} className="logo"
              onClick={() => onClick(-1)}
            ></Link>
          </div>

          <ul className="depth1__sitemap">
            {
              headerGnbSitemap.map((li, index) => {
                return <li key={index}>
                  <Link to={li.path}
                    onMouseEnter={() => onMouseEnter(index)}
                    onMouseLeave={onMouseLeave}
                    onClick={() => onClick(index)}
                    className={depth2.focusIndex === index ? 'focused' : ''}
                  >{li.h5}</Link>
                </li>
              })
            }
          </ul>
        </div>

        <div className="--right">

          <ul className="depth1__community">
            <li>
              <Link to="/app/OSP0111"
                onClick={() => onClick(4)}
                onMouseEnter={() => onMouseEnter(4)}
                onMouseLeave={onMouseLeave}
                className={4 === depth2.focusIndex ? "focused" : ''}
              >Tmax</Link></li>

            <li>
              <a href="https://kr.tmaxsoft.com/recruit/recruitNotice.do" target={"_blank"} rel="noreferrer">
                
                <i className="icon icon-open_in_new">
                  <svg width="9" height="18" viewBox="0 0 9 18" xmlns="http://www.w3.org/2000/svg">
                    <path fillRule="evenodd" clipRule="evenodd" d="M1.9263 3.324H8.0013C8.37409 3.324 8.6763 3.62621 8.6763 3.999V10.074C8.6763 10.4468 8.37409 10.749 8.0013 10.749C7.62851 10.749 7.3263 10.4468 7.3263 10.074V5.62859L1.2786 11.6763L0.324005 10.7217L6.37171 4.674H1.9263C1.55351 4.674 1.2513 4.37179 1.2513 3.999C1.2513 3.62621 1.55351 3.324 1.9263 3.324Z" />
                  </svg>
                </i>
              </a>
            </li>
          </ul>

          <ul className="depth1__user">
            <li><Link to="/login" onClick={onClick}></Link></li>
            <li><Link to="/signin" onClick={onClick}></Link></li>
          </ul>



          <div className="depth1__box">
            <button className="btn-toggle-depth1">
              <i className="icon icon_btn-toggle">
                <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path fillRule="evenodd" clipRule="evenodd" d="M1.5 4.5C1.5 4.08579 1.83579 3.75 2.25 3.75H17.75C18.1642 3.75 18.5 4.08579 18.5 4.5C18.5 4.91421 18.1642 5.25 17.75 5.25H2.25C1.83579 5.25 1.5 4.91421 1.5 4.5ZM1.5 10C1.5 9.58579 1.83579 9.25 2.25 9.25H17.75C18.1642 9.25 18.5 9.58579 18.5 10C18.5 10.4142 18.1642 10.75 17.75 10.75H2.25C1.83579 10.75 1.5 10.4142 1.5 10ZM2.25 14.75C1.83579 14.75 1.5 15.0858 1.5 15.5C1.5 15.9142 1.83579 16.25 2.25 16.25H17.75C18.1642 16.25 18.5 15.9142 18.5 15.5C18.5 15.0858 18.1642 14.75 17.75 14.75H2.25Z" fill="white"/>
                </svg>
              </i></button>
            
          </div>

        </div>
      </div>

      <div className="gnb__depth2">
        {
          headerGnbDepth2.map(({ h5, span, ul }, index) => {
            
            const uls = ul.map((li, index) => {
              return <li key={index}>
                <Link to={li.path}
                  onClick={onClick}
                >{li.text}</Link>
              </li>
            });

            return <div key={index}
              onMouseEnter={() => onMouseEnter(index)}
              onMouseLeave={onMouseLeave}
              onClick={() => onClick(index)}

              className={'depth2 ' + (depth2.index === index ? depth2.state : 'depth2-close')}>

              <div className='depth2-container width1440'>
                <div>
                  <h5>{h5}</h5>
                  <p>{span}</p>
                  <button>{""}</button>
                </div>
                <ul>{uls}</ul>
              </div>

            </div>
          })
        }
      </div>
    </nav>
  </header>
}

