import { useState, useEffect } from 'react';

import '../../styles/layout/_alertpopup.scss';

export default function AlertPopup({props}){
  const [alert, setAlert] = useState({
    state: false,
    cate: '',
    // p: ' ?     .',
    ...props
  });

  useEffect(() => {
    console.log('useeffect')
    setAlert({
      cate: '',
      ...props
    })
  }, [props])


  return <>
  <div className={alert.state?"popup-wrapper":"popup-wrapper --n"}>
    <div className="popup-container">

      <div className="--top">
        <span className='popup-span'>{alert.cate}</span>
        <button 
          className="btn-close"
          onClick={() => {
            setAlert({
              ...alert,
              state: false
            })
          }}
        >
          <i className="icon icon-close"></i></button>
      </div>

      <p className='popup-p'>
        {' ?'} <br />
        {'    .'}
      </p>

      <div className="--bottom">
        <button className="btn-cancle"></button>
        <button className="btn-delete">{alert.cate}</button>
      </div>

    </div>
  </div>
  </>
}